<template>
    <div id="app">
        <div id="logo">Sui</div>
        <full-page :options="options" @after-load="afterLoad" id="fullpage">
            <div class="section">
                <h3>Welecome</h3>
            </div>
            <div class="section">
                <h3>This is a station for Sui</h3>
            </div>
            <div class="section">
                <h3><a href="http://www.asui.xyz">Go View</a></h3>
            </div>
        </full-page>
    </div>
</template>
<script>
import FullPage from "@/plugin/FullPage.vue";
import fullPageMixin from "@/plugin/fullPageMixin";
export default {
    mixins: [fullPageMixin],
    components: {
        FullPage
    },
    data() {
        return {
            options: {
                afterLoad: this.afterLoad,
                scrollBar: false,
                menu: "#menu",
                navigation: true,
                anchors: ["page1", "page2", "page3"],
                sectionsColor: [
                    "#41b883",
                    "#ff5f45",
                    "#0798ec",
                    "#fec401",
                    "#1bcee6",
                    "#ee1a59",
                    "#2c3e4f",
                    "#ba5be9",
                    "#b4b8ab"
                ]
            }
        };
    },
    methods: {
        afterLoad() {
            console.log("Emitted 'after load' event.");
        }
    }
};
</script>
<style>
#app {
    font-family: "Avenir", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
</style>



